import React, { Fragment, useEffect, useState } from "react";
import { Row, Button, Card } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import { ContextMenuTrigger } from "react-contextmenu";
import classnames from "classnames";
import mKlinik from '../../../Controler/c_Klinik'
import mUser from '../../../Controler/c_User'
import Skeleton from 'react-loading-skeleton';
import ModalKlinik from './Modal.ManajemenKlinik'
import Notifikasi from '../../../helpers/Notifikasi'
import Confirm from '../../../helpers/Confirm'
import { isArray } from "util";

const ManajemenKlinik = (props) => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [listKlinik, setListKlinik] = useState([])
    const [user, setUser] = useState({})
    const [detailKlinik, setDetailKlinik] = useState({})
    const [isConfirm, setIsConfirm] = useState(false)
    const [textConfirm, setTextConfirm] = useState("")
    const [isModal, setIsModal] = useState(false)
    useEffect(() => {
        mKlinik.listKlinik(authLogin.clientID, authLogin.token).then(resp => {
            setListKlinik(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, isReload]);
    useEffect(() => {
        mUser.dataUser(authLogin.clientID, authLogin.token, authLogin.username, "admin").then(resp => {
            setUser(resp);
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, authLogin.username])
    const handleSelectKlinik = data => {
        if (authLogin.jenisUser === "admin") {
            mUser.selectKlinikAdmin(authLogin.clientID, authLogin.token, data.kodeklinik, data.namaklinik, authLogin.username).then(resp => {
                if (resp) {
                    const loginUser = {
                        clientID: authLogin.clientID,
                        token: authLogin.token,
                        username: authLogin.username,
                        jenisUser: authLogin.jenisUser,
                        superadmin: false,
                        klinik: {
                            kodeklinik: data.kodeklinik,
                            namaklinik: data.namaklinik
                        }

                    }
                    localStorage.setItem('keminaAdmin', JSON.stringify(loginUser));
                    window.location.href = '/app/dashboard';
                }
            }).catch(err => {
                console.error(err);
            })
        } else {
            mUser.selectKlinikPasien(authLogin.clientID, authLogin.token, data.kodeklinik, data.namaklinik, authLogin.username).then(resp => {
                if (resp) {
                    const loginUser = {
                        clientID: authLogin.clientID,
                        token: authLogin.token,
                        username: authLogin.username,
                        jenisUser: authLogin.jenisUser,
                        superadmin: false,
                        klinik: {
                            kodeklinik: data.kodeklinik,
                            namaklinik: data.namaklinik
                        }

                    }
                    localStorage.setItem('keminaAdmin', JSON.stringify(loginUser));
                    window.location.href = '/app/dashboard';
                }
            }).catch(err => {
                console.error(err);
            })
        }
    }
    const handleAdd = () => {
        setDetailKlinik({})
        setIsModal(true)
    }
    const handleEdit = (data) => {
        setDetailKlinik(data)
        setIsModal(true)
    }
    const confirmHapus = (data) => {
        setDetailKlinik(data)
        setTextConfirm(`Yakin ingin hapus ${data.namaklinik}? Data tidak dapat dikembalikan setelah hapus`)
        setIsConfirm(true)
    }
    const handleHapus = () => {
        mKlinik.hapusKlinik(authLogin.clientID, authLogin.token, detailKlinik.kodeklinik).then(resp => {
            if (resp) {
                Notifikasi("success", `${detailKlinik.namaklinik} Berhasil dihapus`, "")
                setIsReload(true)
                setIsConfirm(false)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    return (
        <Fragment>
            <Row>
                <Colxx xxs="12">
                    <div className="mb-2">
                        <h1>
                            Manajemen Klinik
                        </h1>

                        <div className="text-zero top-right-button-container">
                            {user.superadmin && (
                                <Button
                                    color="primary"
                                    size="lg"
                                    className="top-right-button"
                                    onClick={handleAdd}
                                >
                                    Add Klinik
                                </Button>
                            )}
                        </div>
                    </div>

                    <Separator className="mb-5" />
                </Colxx>
            </Row>
            <Row>
                <Colxx xxs="12" className="mb-4">
                    <ContextMenuTrigger id="menu_id" >
                        {isReload && (
                            <Card className={classnames("d-flex flex-row")}
                            >
                                <Skeleton />
                                <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                                    <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
                                        <p className="list-item-heading mb-1 truncate">
                                            <Skeleton />
                                        </p>
                                        <p className="mb-1 text-muted text-small w-40 w-sm-100">
                                            <Skeleton />
                                        </p>
                                    </div>
                                    <div className="custom-control custom-checkbox pl-1 align-self-center pr-4">
                                        <Skeleton />
                                    </div>
                                </div>
                            </Card>
                        )}
                        {listKlinik.map((el, index) => {
                            return (
                                <Card key={index} className={classnames("d-flex flex-row")}
                                >
                                    <img
                                        alt={'name'}
                                        src="https://img.icons8.com/cotton/2x/clinic.png"
                                        className="list-thumbnail responsive border-0 card-img-left"
                                    />
                                    <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                                        <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
                                            <p className="list-item-heading mb-1 truncate">
                                                {el.namaklinik}
                                            </p>
                                            <p className="mb-1 text-muted text-small w-40 w-sm-100">
                                                {el.alamat}
                                            </p>
                                        </div>
                                        <div className="custom-control custom-checkbox pl-1 align-self-center pr-4">
                                            <Button onClick={handleSelectKlinik.bind(this, el)}>Pilih Klinik</Button>
                                            {user.superadmin && (
                                                <Button style={{ marginLeft: 3, marginRight: 3 }} color="warning" onClick={handleEdit.bind(this, el)}>Edit</Button>
                                            )}
                                            {user.superadmin && (
                                                <Button color="danger" onClick={confirmHapus.bind(this, el)}>Hapus</Button>
                                            )}
                                        </div>
                                    </div>
                                </Card>
                            )
                        })}
                    </ContextMenuTrigger>
                </Colxx>
            </Row>

            {isModal && (
                <ModalKlinik open={isModal} setOpen={setIsModal} clientID={authLogin.clientID} token={authLogin.token} setIsReload={setIsReload} detailKlinik={detailKlinik} />
            )}
            {isConfirm && (
                <Confirm  open={isConfirm} setOpen={setIsConfirm} textConfirm={textConfirm} oke={handleHapus}/>
            )}
        </Fragment>
    )
}
export default ManajemenKlinik;