import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { TextField } from '@material-ui/core';
import mKlinik from '../../../Controler/c_Klinik'
import validate from 'validate.js'
import Notifikasi from '../../../helpers/Notifikasi'
import { isArray } from "util";


const ModalManajemenKlinik = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailKlinik || null
    console.log(props)
   
    const [form, setForm] = useState({
        kodeklinik: '',
        namaklinik: '',
        alamat: "",
        hp: "",
        telp: "",
    })
    useEffect(() => {
        if (detail.kodeklinik) {
            setForm({
                kodeklinik: detail.kodeklinik,
                namaklinik: detail.namaklinik,
                tanggalSelesai: detail.tanggalSelesai,
                alamat: detail.alamat,
                hp: detail.hp,
                telp: detail.telp,
            })
        }
        return () => {

        }
    }, [detail])
    const handleClose = () => {
        props.setOpen(false)
    }
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...form }
        newForm[key] = value;
        setForm(newForm);
    }
    const validateForm = () => {
        const { namaklinik, alamat, hp, telp } = form;
        const promForm = new Promise((resolve, reject) => {
            const constraint = {
                namaklinik: {
                    length: {
                        minimum: 3,
                        message: "Nama klinik minimal terdiri dari 3 huruf"
                    },
                },
                alamat: {
                    length: {
                        minimum: 3,
                        message: "Alamat minimal terdiri dari 3 huruf"
                    },
                },
                hp: {
                    length: {
                        minimum: 3,
                        message: "HP minimal terdiri dari 3 dgit"
                    },
                },
                telp: {
                    length: {
                        minimum: 3,
                        message: "Telp minimal terdiri dari 3 digit"
                    },
                }
            }
            const validator = validate({ namaklinik, alamat, hp, telp }, constraint, { fullMessages: false });
            if (validator === undefined) {
                resolve();
            } else {
                reject(validator);
            }
        })
        return Promise.all([promForm]);
    }
    const handleSimpan = () => {
        validateForm().then(() => {
            const { kodeklinik, namaklinik, alamat, hp, telp } = form;
            mKlinik.simpanKlinik(clientID, token, kodeklinik, namaklinik, alamat, hp, telp ).then(resp => {
                if (resp) {
                    Notifikasi("success","Berhasil disimpan", "")
                    props.setIsReload(true)
                    handleClose()
                } else {
                    Notifikasi("error","Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error",err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error",err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }).catch(err => {
            const keys = Object.keys(err);
            for (const key of keys) {
                Notifikasi("warning", err[key])
            }
        })
    }
    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>
                {detail.kodepromo ? ("Update") : ("Add")} Klinik
                    </ModalHeader>
            <ModalBody>
                <Row>
                    <Col xs={12} md={12}>
                        <TextField
                            id="namaklinik"
                            name="namaklinik"
                            label="Nama Klinik"
                            fullWidth
                            value={form.namaklinik}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="alamat"
                            name="alamat"
                            label="Alamat"
                            fullWidth
                            value={form.alamat}
                            onChange={handleChange}
                            variant="outlined"
                            multiline
                            rows="3"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="telp"
                            name="telp"
                            label="Telp"
                            fullWidth
                            value={form.telp}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="hp"
                            name="hp"
                            label="HP"
                            fullWidth
                            value={form.hp}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                <Button color="primary" onClick={handleSimpan}>Save Klinik</Button>{" "}
            </ModalFooter>
        </Modal>
    )
}

export default ModalManajemenKlinik;