import GQL from '../helpers/GQL'


const Klinik = {
    listKlinik: (clientID, token) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    listKlinik{
                        kodeklinik
                        namaklinik
                        alamat
                        hp
                        telp
                  }
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.listKlinik) return (resolve(resp.data.listKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    simpanKlinik: (clientID, token, kodeklinik, namaklinik, alamat, hp, telp) => {
        return new Promise((resolve, reject) => {
            const query = `
                mutation {
                    simpanKlinik(input:{
                        kodeklinik: "${kodeklinik}",
                        namaklinik:"${namaklinik}", 
                        alamat:"${alamat}", 
                        hp:"${hp}", 
                        telp:"${telp}",
                        aktif: true
                    })
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    hapusKlinik: (clientID, token, kodeklinik) => {
        return new Promise((resolve, reject) => {
            const query = `
                mutation {
                    simpanKlinik(input:{
                        kodeklinik: "${kodeklinik}",
                        aktif: false
                    })
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
};

export default Klinik;
